<template>
  <div>
    <h2 v-if="doneLists.length == 0">还未有完成的任务</h2>
    <div v-else>
      <h2>已完成的任务列表</h2>
      <div class="doneLists">
        <doneList
          v-for="(list, index) in doneLists"
          :list="list"
          :key="index"
        ></doneList>
      </div>
    </div>
  </div>
</template>

<script>
import doneList from "@/components/DoneList";
export default {
  name: "doneLists",
  data() {
    return {
      doneLists: this.$store.state.doneLists,
    };
  },
  components: {
    doneList,
  },
};
</script>

<style>
.DoneList:nth-child(2n + 1) {
  background: #aaa;
}
</style>