<template>
    <div class="DoneList">
      <div class="describe">{{ list.message }}</div>
      <div class="ptime">耗费时间：{{ time }}</div>
      <div class="ctime">创建时间：{{ list.date }}</div>
    </div>
</template>

<script>
export default {
  name: "doneList",
  props: {
    list: Object
  },
  computed: {
    time() {
      switch (this.list.standard) {
        case "seconds":
          return this.list.time + "s";
        case "minutes":
          return this.list.time + "min";
        case "hours":
          return this.list.time + "h";
      }
    }
  }
};
</script>

<style>
.DoneList {
  display: flex;
  justify-content: space-around;
  padding: 10px 0 10px 0;
}

.DoneList .describe{
  flex: 1;
  margin-left: 20px;
}

.DoneList .ptime, .DoneList .ctime{
  flex: 1;
}
</style>